import { Controller } from '@ali/4ever-cangjie';
import { HEADING_SET, HEADING_TRANSFORM } from '../actions';
import setHeading from '../commands/setHeading';
import transform from '../commands/transform';

export default function onAction(action, controller: Controller, next: Function) {
  const { type, payload } = action;
  if (type === HEADING_SET) {
    const { heading } = payload;
    setHeading(controller, heading);
  }
  if (type === HEADING_TRANSFORM) {
    const { heading } = payload;
    controller.command(transform, heading);
  }
  return next();
}
